@import url(https://fonts.googleapis.com/css?family=Indie+Flower|Nunito+Sans:400,700,800&display=swap&subset=latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%;
  box-sizing: border-box;
  font-family: "Nunito Sans", sans-serif; }

.DadJokes {
  min-height: 100vh;
  background: -webkit-linear-gradient(315deg, #93cede 0%, #75bdd1 50%, #b749bd 50%, #b749bd 100%);
  background: linear-gradient(135deg, #93cede 0%, #75bdd1 50%, #b749bd 50%, #b749bd 100%);
  display: flex;
  justify-content: center;
  align-items: center; }
  @media only screen and (max-width: 50em) {
    .DadJokes {
      flex-direction: column; } }

.Board {
  background-color: white;
  height: 75vh;
  position: relative;
  box-shadow: 0 0 1rem 0.3rem rgba(0, 0, 0, 0.4);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 110rem;
  z-index: 1;
  overflow-y: scroll; }
  .Board::-webkit-scrollbar {
    width: 0;
    display: none; }
  @media only screen and (max-width: 50em) {
    .Board {
      width: 100%;
      height: 80vh; } }

.Sidebar {
  background-color: #8b69c6;
  height: 85vh;
  border-radius: 5px;
  box-shadow: 0 0 1rem 0.3rem rgba(0, 0, 0, 0.4);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40rem;
  position: relative;
  z-index: 2;
  color: #fff; }
  @media only screen and (max-width: 50em) {
    .Sidebar {
      width: 100%;
      flex-direction: row;
      height: 20vh;
      justify-content: space-evenly; } }
  .Sidebar__h1 {
    font-size: 4rem;
    font-weight: 800;
    letter-spacing: 4px; }
    @media only screen and (max-width: 50em) {
      .Sidebar__h1 {
        font-size: 2rem;
        letter-spacing: normal;
        letter-spacing: initial; } }
  .Sidebar__cursive {
    font-family: "Indie Flower", cursive;
    text-transform: uppercase; }
  .Sidebar__icon {
    font-size: 14rem;
    margin: 4rem 0;
    box-shadow: 0 1rem 1rem 0.3rem rgba(0, 0, 0, 0.4);
    border-radius: 50%; }
    @media only screen and (max-width: 50em) {
      .Sidebar__icon {
        font-size: 4rem;
        margin: 1rem 1rem;
        box-shadow: 0 0.4rem 0.6rem rgba(0, 0, 0, 0.4); } }
  .Sidebar__btn {
    background: center -webkit-linear-gradient(315deg, #93cede 0%, #75bdd1 50%, #b749bd 50%, #b749bd 100%);
    background: center linear-gradient(135deg, #93cede 0%, #75bdd1 50%, #b749bd 50%, #b749bd 100%);
    background-size: 300%;
    border: none;
    border-radius: 25px;
    padding: 20px 35px;
    color: inherit;
    font-size: 1.6rem;
    letter-spacing: 1px;
    cursor: pointer;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    box-shadow: 0 0.3rem 0.8rem 0.2rem rgba(0, 0, 0, 0.4);
    outline: none; }
    .Sidebar__btn:hover {
      background-position: -50% -100%; }
    .Sidebar__btn:focus {
      opacity: .8; }
    .Sidebar__btn:active {
      -webkit-transform: translateY(-2px);
              transform: translateY(-2px);
      background-position: 0 100%; }
    @media only screen and (max-width: 50em) {
      .Sidebar__btn {
        font-size: 1.2rem;
        padding: 10px 15px; } }

.Jokes {
  list-style: none;
  width: 100%;
  height: 100%; }

.Joke {
  display: flex;
  padding: 2rem 0;
  align-items: center; }
  .Joke:not(:last-child) {
    border-bottom: 2px solid #dcdde1; }
  .Joke__vote {
    margin: 0 3rem; }
    @media only screen and (max-width: 50em) {
      .Joke__vote {
        margin: 0 1rem; } }
  .Joke__txt {
    margin-right: auto;
    font-size: 1.5rem;
    text-align: left; }
  .Joke__icon {
    margin: 0 3rem; }
    @media only screen and (max-width: 50em) {
      .Joke__icon {
        margin: 0 1rem; } }
  .Joke__new-indicator {
    color: #b749bd;
    font-size: 1.3rem; }

.vote {
  display: flex;
  align-items: center;
  color: #cbccd0;
  text-align: center; }
  @media only screen and (max-width: 50em) {
    .vote {
      flex-direction: column;
      align-content: space-between; } }
  .vote__count {
    border-radius: 50%;
    margin: 0 1rem;
    font-size: 1.5rem;
    border: 2px solid;
    width: 4.5rem;
    height: 4.5rem;
    line-height: 4.3rem;
    color: #a9aab0; }
    @media only screen and (max-width: 50em) {
      .vote__count {
        margin: 0; } }
    .vote__count--great {
      border-color: #0eeb19; }
    .vote__count--good {
      border-color: #0fa016; }
    .vote__count--mediocrate {
      border-color: #b3d10a; }
    .vote__count--bad {
      border-color: #e6a417; }
    .vote__count--horrible {
      border-color: #e64017; }
  .vote__arrow {
    font-size: 4rem;
    font-weight: bold;
    margin: 0 .5rem;
    cursor: pointer;
    -webkit-transition: color 0.2s;
    transition: color 0.2s; }
    .vote__arrow:hover {
      color: #dcdde1; }

.IconIndicator {
  font-size: 4rem; }

